@font-face {
  font-family: "poppins";
  src: url("../fonts/poppins-regular.woff2") format("woff2"), url("../fonts/poppins-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "poppins";
  src: url("../fonts/poppins-medium.woff2") format("woff2"), url("../fonts/poppins-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "poppins";
  src: url("../fonts/poppins-bold.woff2") format("woff2"), url("../fonts/poppins-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "cormorant";
  src: url("../fonts/cormorant-garamond-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-italic.woff") format("woff");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "cormorant";
  src: url("../fonts/cormorant-garamond-medium-italic.woff2") format("woff2"), url("../fonts/cormorant-garamond-medium-italic.woff") format("woff");
  font-weight: 500;
  font-style: italic;
  font-display: swap;
}
@font-face {
  font-family: "cormorant";
  src: url("../fonts/cormorant-garamond-semi-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-semi-bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "cormorant";
  src: url("../fonts/cormorant-garamond-bold.woff2") format("woff2"), url("../fonts/cormorant-garamond-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.landing-header {
  width: 100%;
}

.landing-header__wrapper {
  padding: 0 15%;
  margin: 0 auto;
  align-items: center;
}

.landing-header__wrapper h1 {
  font-size: 32px;
  line-height: 137.5%;
  color: #1D1613;
  font-family: "poppins", "Arial", serif;
  font-weight: 700;
  font-style: normal;
}

.landing-header__navigation ol {
  margin: 0;
  padding: 0;
}

.landing-header__navigation li::marker {
  font-size: 24px;
  line-height: 150%;
  color: #1D1613;
  font-family: "poppins", "Arial", serif;
  font-weight: 500;
  font-style: normal;
}

.landing-header__navigation a {
  font-size: 24px;
  line-height: 150%;
  color: #1D1613;
  font-family: "poppins", "Arial", serif;
  font-weight: 500;
  font-style: normal;
}

.landing-header__navigation a:hover {
  color: #ffffff;
}

.landing-body {
  background-color: #F0C8D9;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}
.overlay--active {
  opacity: 1;
  visibility: visible;
  z-index: 3;
}

.page__body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

.page-header {
  width: 100%;
  font-family: "poppins", "Arial", serif;
  font-weight: 500;
  font-style: normal;
}
@media (max-width: 1023.99px) {
  .page-header {
    height: 100px;
    transition: height 1s ease 0s;
  }
}

.page-header--open {
  background-color: #A87B62;
  z-index: 50;
  overflow: hidden;
  height: 100vh;
  overflow-y: auto;
  transition: height 1s ease 0s;
}

.page-header--open .page-header__logo svg {
  fill: #ffffff;
}

.page-header--open .page-header__link--cart svg {
  display: none;
}
@media (max-width: 1023.99px) {
  .page-header--open .page-header__link--cart svg {
    cursor: pointer;
    display: block;
    stroke: #ffffff;
    width: 18px;
    height: 16px;
    transition: stroke 0.3s ease 0s;
  }
}

.page-header--open .page-header__link--cart:hover svg {
  stroke: #1D1613;
  transition: stroke 0.3s ease 0s;
}

@media (max-width: 1023.99px) {
  .page-header--open .page-header__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 25px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: 0 7px 0 0 #ffffff, 0 14px 0 0 #ffffff;
    transition: all 0.3s ease 0s;
  }
}

.page-header--open .page-header__toggle:hover::before {
  background-color: #1D1613;
  box-shadow: 0 7px 0 0 #1D1613, 0 14px 0 0 #1D1613;
}

.page-header__wrapper {
  padding: 10px 7%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 1023.99px) {
  .page-header__wrapper {
    position: relative;
    padding: 30px 5% 80px;
  }
}

.page-header__wrapper a:hover {
  color: #1D1613;
}

.page-header__logo svg {
  fill: #A87B62;
}

.page-header__search {
  display: flex;
  align-items: center;
  position: relative;
}
@media (max-width: 1023.99px) {
  .page-header__search {
    width: 100%;
    order: 3;
    margin: 55px 0 0 0;
  }
}

.page-header__search input {
  border: none;
  margin: 0 0 0 15px;
  padding: 7px 0;
}
@media (max-width: 1023.99px) {
  .page-header__search input {
    width: 100%;
    background-color: rgba(255, 255, 255, 0.15);
    border-radius: 21px;
    margin: 0;
  }
}

.page-header__search svg {
  stroke: #000000;
}
@media (max-width: 1023.99px) {
  .page-header__search svg {
    stroke: #ffffff;
    fill: #ffffff;
    position: absolute;
    left: 10px;
    opacity: 0.6;
  }
}

.page-header__search input::placeholder {
  color: #9B9997;
  font-size: 14px;
  line-height: 150%;
}
@media (max-width: 1023.99px) {
  .page-header__search input::placeholder {
    color: transparent;
  }
}

.page-header__links a {
  font-size: 14px;
  line-height: 150%;
  color: #1D1613;
}

@media (max-width: 1023.99px) {
  .page-header__link--login {
    display: none;
  }
}

.page-header__link--cart {
  margin: 0 0 0 35px;
}
@media (max-width: 1023.99px) {
  .page-header__link--cart {
    margin: 0;
  }
}

.page-header__link--cart svg {
  display: none;
}
@media (max-width: 1023.99px) {
  .page-header__link--cart svg {
    cursor: pointer;
    display: block;
    stroke: #1D1613;
    width: 18px;
    height: 16px;
    transition: stroke 0.3s ease 0s;
  }
}

.page-header__link--cart:hover svg {
  stroke: #A87B62;
}

@media (max-width: 1023.99px) {
  .page-header__link--cart span {
    display: none;
  }
}

.page-header__link--cart span:last-of-type {
  margin: 0 0 0 5px;
}
@media (max-width: 1023.99px) {
  .page-header__link--cart span:last-of-type {
    margin: 0;
  }
}

.page-header__toggle {
  display: none;
}
@media (max-width: 1023.99px) {
  .page-header__toggle {
    display: block;
    cursor: pointer;
    border: none;
    background-color: transparent;
    margin: 0;
    padding: 0;
    width: 25px;
    height: 16px;
    position: relative;
  }
}

@media (max-width: 1023.99px) {
  .page-header__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 25px;
    height: 2px;
    background-color: #1D1613;
    box-shadow: 0 7px 0 0 #1D1613, 0 14px 0 0 #1D1613;
    transition: all 0.3s ease 0s;
  }
}

.page-header__toggle:hover::before {
  background-color: #A87B62;
  box-shadow: 0 7px 0 0 #A87B62, 0 14px 0 0 #A87B62;
}

.page-header__menu {
  width: 100%;
  padding: 44px 0 0 0;
}
@media (max-width: 1023.99px) {
  .page-header__menu {
    order: 5;
  }
}

.page-header__menu ul {
  list-style: none;
}

.page-header__goods-list {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023.99px) {
  .page-header__goods-list {
    flex-direction: column;
    margin: 0 0 55px 0;
  }
}

.page-header__goods-list a {
  font-size: 16px;
  line-height: 150%;
  color: #1D1613;
}
@media (max-width: 1023.99px) {
  .page-header__goods-list a {
    font-size: 24px;
    color: #ffffff;
  }
}

.page-header__goods-list li:not(:last-child) {
  margin: 0 0 0 34px;
}
@media (max-width: 1023.99px) {
  .page-header__goods-list li:not(:last-child) {
    margin: 0 0 40px 0;
  }
}

.page-header__data-list {
  display: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023.99px) {
  .page-header__data-list {
    display: block;
    margin: 0 0 55px 0;
  }
}

@media (max-width: 1023.99px) {
  .page-header__data-list li:not(:last-child) {
    margin: 0 0 30px 0;
  }
}

@media (max-width: 1023.99px) {
  .page-header__data-list a {
    font-size: 18px;
    line-height: 127.7777777778%;
    color: rgba(255, 255, 255, 0.75);
  }
}

.page-header__menu-link-login {
  display: none;
}
@media (max-width: 1023.99px) {
  .page-header__menu-link-login {
    display: block;
    font-size: 18px;
    line-height: 127.7777777778%;
    color: #ffffff;
  }
}

.page-main {
  flex: 1 1 auto;
  z-index: 1;
}